@font-face {
  font-family: "Alem";
  src: url(../fonts/alem-regular.woff) format("woff"), url(../fonts/alem-regular.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Alem";
  src: url(../fonts/alem-bold.woff) format("woff"), url(../fonts/alem-bold.woff2) format("woff2");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
.header {
  padding: 40px 0;
  background-color: #fff;
  position: fixed;
  z-index: 30;
  width: 100%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .header__container {
    justify-content: flex-end;
  }
}
@media screen and (max-width: 768px) {
  .header__logo {
    margin-right: auto;
  }
}
.header__list {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin: 0 55px;
}
@media screen and (max-width: 1024px) {
  .header__list {
    margin: 0 15px;
  }
}
@media screen and (max-width: 768px) {
  .header__list {
    display: none;
  }
}
.header__list-link {
  font-weight: 700;
  letter-spacing: -0.54px;
  font-size: 18px;
  color: #000;
  transition: 0.3s all;
}
.header__list-link:hover {
  color: #74b461;
}
.header__social {
  display: flex;
  gap: 0 10px;
}
@media screen and (max-width: 768px) {
  .header__social {
    gap: 0 20px;
    margin-right: 30px;
  }
}
.header__social-item {
  width: 32px;
  height: 32px;
  position: relative;
}
.header__social-link {
  position: absolute;
  width: 100%;
  height: 100%;
}
.header__burger {
  display: none;
  position: relative;
  width: 40px;
  height: 40px;
}
.header__burger.active span:nth-child(2) {
  width: 0;
}
.header__burger.active span:first-child {
  top: 50%;
  transform: translateY(-50%) translateX(-50%) rotateZ(45deg);
  left: 50%;
}
.header__burger.active span:last-child {
  top: 50%;
  transform: translateY(-50%) translateX(-50%) rotateZ(-45deg);
  left: 50%;
}
.header__burger:hover span {
  background-color: #74b461;
}
.header__burger span {
  position: absolute;
  width: 38px;
  height: 5px;
  background-color: #000;
  border-radius: 5px;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: none;
  transition: 0.3s all;
}
.header__burger span:last-child {
  bottom: 5px;
}
.header__burger span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
}
.header__burger span:first-child {
  top: 5px;
}
@media screen and (max-width: 768px) {
  .header__burger {
    display: inline-block;
  }
}

.promo {
  height: 688px;
  max-width: 100vw;
  width: 100%;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #f3f3f3;
  margin-top: 117px;
}
@media screen and (max-width: 500px) {
  .promo {
    height: 600px;
  }
}
.promo__container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.promo__info {
  padding-top: 83px;
  position: relative;
}
@media screen and (max-width: 1024px) {
  .promo__info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 30px;
  }
}
@media screen and (max-width: 500px) {
  .promo__info {
    align-items: center;
  }
}
.promo__text {
  position: relative;
}
.promo__title {
  font-size: 80px;
  font-weight: 700;
  line-height: 75px;
  color: #000;
  position: relative;
}
.promo__title span {
  color: #74b461;
  text-transform: uppercase;
}
@media screen and (max-width: 1024px) {
  .promo__title {
    font-size: 60px;
    line-height: 69px;
  }
}
@media screen and (max-width: 768px) {
  .promo__title {
    font-size: 48px;
    line-height: 54px;
  }
}
@media screen and (max-width: 500px) {
  .promo__title {
    font-size: 34px;
    line-height: 40px;
    text-align: center;
  }
}
@media screen and (max-width: 380px) {
  .promo__title {
    font-size: 30px;
  }
}
.promo__date {
  position: absolute;
  display: inline-block;
  line-height: 30px;
  bottom: 19%;
  right: 12%;
  padding: 10px;
  font-size: 30px;
  background-color: #e8cc3b;
  font-weight: bold;
  text-transform: uppercase;
  color: #000;
  transform: rotate(-15deg);
  font-weight: bold;
}
@media screen and (max-width: 1024px) {
  .promo__date {
    width: max-content;
    position: static;
    transform: none;
  }
}
@media screen and (max-width: 768px) {
  .promo__date {
    font-size: 24px;
    line-height: 28px;
  }
}
@media screen and (max-width: 500px) {
  .promo__date {
    font-size: 18px;
    line-height: 20px;
  }
}
.promo__button {
  min-height: 65px;
  height: auto;
  max-width: 380px;
  width: 100%;
  margin-top: 55px;
  background-color: #000;
  transition: 0.3s all;
}
.promo__button:hover {
  background-color: #74b461;
}
@media screen and (max-width: 1024px) {
  .promo__button {
    margin-top: 0;
  }
}
@media screen and (max-width: 500px) {
  .promo__button {
    margin: auto;
    padding: 16px;
    font-size: 18px;
    line-height: 20px;
    min-height: auto;
  }
}

.schedule {
  padding-bottom: 113px;
  background-color: #f3f3f3;
}
.schedule__container {
  display: flex;
  flex-direction: column;
  gap: 27px 0;
}
.schedule__title {
  text-align: left;
  line-height: 70px;
}
.schedule__triggers {
  display: flex;
  align-items: center;
  gap: 0 20px;
}
@media screen and (max-width: 768px) {
  .schedule__triggers {
    flex-wrap: wrap;
    gap: 20px;
  }
}
.schedule__trigger {
  background-color: #e4e4e4;
  padding: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #959695;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  transition: 0.3s all;
}
.schedule__trigger.active, .schedule__trigger:hover {
  background-color: #e8cc3b;
  color: #000;
}
.schedule__map {
  display: inline-block;
  margin-left: auto;
  font-size: 18px;
  font-weight: 700;
  color: #000;
  position: relative;
  transition: 0.3s all;
}
@media screen and (max-width: 768px) {
  .schedule__map {
    width: 100%;
    text-align: left;
  }
}
.schedule__map::after {
  content: "";
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background-color: black;
  transition: width 0.3s ease, left 0.3s ease;
  transform: translateX(-50%);
  transition: 0.3s all;
}
.schedule__map:hover::after {
  width: 100%;
}
.schedule__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 колонки */
  gap: 16px;
}
@media screen and (max-width: 1024px) {
  .schedule__content {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .schedule__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
.schedule__event {
  display: none;
  flex-direction: column;
  overflow: hidden;
}
.schedule__event.active {
  display: flex;
}
.schedule__event-picture {
  width: 100%;
  padding-top: 100%; /* Это сделает фото квадратным */
  position: relative;
  margin-bottom: 20px;
}
.schedule__event-img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: center;
  z-index: 0;
}
.schedule__event-price {
  color: #000;
  font-size: 18px;
  font-weight: bold;
}
@media screen and (max-width: 768px) {
  .schedule__event-price {
    font-size: 14px;
  }
}
.schedule__event-description {
  display: flex;
  flex-direction: column;
  gap: 10px 0;
}
.schedule__event-title {
  font-size: 20px;
  font-weight: bold;
  color: #000;
  text-transform: uppercase;
}
@media screen and (max-width: 500px) {
  .schedule__event-title {
    font-size: 14px;
  }
}
.schedule__event-date {
  display: flex;
  gap: 0 5px;
}
@media screen and (max-width: 500px) {
  .schedule__event-date {
    flex-direction: column;
    gap: 10px;
  }
}
.schedule__event-date time {
  padding: 5px 10px;
  font-size: 14px;
  font-weight: 700;
  line-height: 120%; /* 16.8px */
  text-transform: uppercase;
}
@media screen and (max-width: 500px) {
  .schedule__event-date time {
    font-size: 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}
.schedule__event-date time:first-child {
  background-color: black;
  color: #fff;
}
.schedule__event-date time:last-child {
  background-color: #e8cc3b;
  color: #000;
}
.schedule__event-address {
  color: #000;
  font-size: 16px;
  line-height: 120%;
  font-weight: 700;
}
@media screen and (max-width: 500px) {
  .schedule__event-address {
    font-size: 14px;
  }
}
.schedule__event-button {
  display: inline-flex;
  font-size: 20px;
  color: #fff;
  background-color: #74b461;
  transition: 0.3s all;
}
@media screen and (max-width: 500px) {
  .schedule__event-button {
    font-size: 14px;
    padding: 10px;
  }
}
.schedule__event-button:hover {
  background-color: #000;
}

.about {
