.video {
  height: 832px;
  max-width: 100vw;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.video__container {
  height: 100%;
  justify-content: center;
  display: flex;
  flex-direction: column;
  gap: 22px 0;
}
.video__title {
  color: #fff;
  line-height: 70px;
  text-align: left;
  letter-spacing: -2.1px;
}
.video__wrapper {
  height: 540px;
  position: relative;
}
.video__wrapper video {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.video__icon {
