}

.statistic {
  background-color: #f3f3f3;
}
.statistic__container {
  transform: translateY(-50%);
  margin-top: auto;
  padding: 50px;
  background-color: #e8cc3b;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 1024px) {
  .statistic__container {
    padding: 20px 50px;
    flex-direction: column;
    gap: 30px 0;
  }
}
@media screen and (max-width: 500px) {
  .statistic__container {
    padding: 20px;
  }
}

.viz {
  opacity: 1;
  transition: 1s;
}

.statistic-list {
  display: flex;
  gap: 0 70px;
}
@media screen and (max-width: 500px) {
  .statistic-list {
    width: 100%;
    gap: 0 10px;
    justify-content: center;
  }
}

.statistic-item {
  display: flex;
  flex-direction: column;
  max-width: calc(33% - 10px);
  width: 100%;
  align-items: center;
  font-weight: bold;
  color: #000;
}
.statistic-item span:first-child {
  opacity: 0;
  text-transform: uppercase;
  font-size: 80px;
}
.statistic-item span:first-child.viz {
  transition: 1s;
  opacity: 1;
}
@media screen and (max-width: 1024px) {
  .statistic-item span:first-child {
    font-size: 60px;
  }
}
@media screen and (max-width: 500px) {
  .statistic-item span:first-child {
    font-size: 40px;
  }
}
.statistic-item span:last-child {
  text-align: center;
  font-size: 18px;
  white-space: nowrap;
}
@media screen and (max-width: 500px) {
  .statistic-item span:last-child {
    font-size: 12px;
  }
}

.statistic-gallery {
  display: flex;
  margin-left: -20px;
}
@media screen and (max-width: 1024px) {
  .statistic-gallery {
    margin-right: -20px;
  }
}
@media screen and (max-width: 500px) {
  .statistic-gallery {
    margin-right: -40px;
  }
}

.statistic-comedian {
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  width: 92px;
  height: 92px;
  margin-left: -15px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  position: absolute; /* Позиционируем фотографии абсолютно */
  top: 0;
  left: 0;
}
@media screen and (max-width: 1024px) {
  .statistic-comedian {
    width: 52px;
    height: 52px;
  }
}

.menu {
  display: none;
  position: fixed;
  top: 121px;
  width: 100%;
  height: 100%;
  min-height: auto;
  background-color: #fff;
  z-index: 290;
  padding-top: 40px;
  align-items: center;
  flex-direction: column;
  transform: translateX(100%);
  transition: all cubic-bezier(0.81, 0.16, 0.41, 0.98) 0.5s;
  overflow: auto;
}
.menu.active {
  visibility: visible;
  transform: translateX(0);
}
@media screen and (max-width: 768px) {
  .menu {
    display: flex;
  }
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.menu__list-link {
  display: inline-flex;
  padding: 10px 20px;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  font-size: 22px;
  font-weight: bold;
  transition: 0.3s all;
  color: #000;
}
.menu__list-link:hover {
  color: #fff;
  background-color: #74b461;
}

.faq {
  padding: 80px 0;
  background-color: #D1EAC9;
}
.faq__title {
  text-align: center;
  margin-bottom: 30px;
}
.faq__accordeon {
  margin-bottom: 20px;
  border-bottom: 1px solid #000;
}
.faq__accordeon-button {
  padding: 20px;
  display: inline-flex;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
  color: #000;
  padding-right: 24px;
  position: relative;
  transition: 0.3s all;
}
.faq__accordeon-button.active::after {
  transform: translateY(-50%) rotate(0deg);
}
.faq__accordeon-button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transition: 0.3s all;
  background-image: url(../images/arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 24px;
  height: 24px;
  transform: translateY(-50%) rotate(-90deg);
}
.faq__accordeon-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s ease-out;
  font-size: 20px;
  padding: 0 20px;
}
.faq__accordeon-answer p {
  padding-bottom: 20px;
}

.merch {
  padding: 80px 0;
  background-color: #F3F3F3;
}
.merch__title {
  margin-bottom: 20px;
  text-align: left;
}
.merch__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Создаем 3 колонки */
  gap: 20px;
}
@media screen and (max-width: 1024px) {
  .merch__content {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .merch__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
.merch__item-wrapper {
  position: relative;
  width: 100%;
  padding-top: 100%; /* Соотношение сторон 1:1 (квадрат) */
  overflow: hidden;
  margin-bottom: 10px;
}
.merch__item-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.merch__item-description {
  display: flex;
  flex-direction: column;
  gap: 10px 0;
}
.merch__item-title, .merch__item-sizes, .merch__item-price {
  font-size: 18px;
  color: #000;
  font-weight: bold;
  letter-spacing: -0.54px;
}
@media screen and (max-width: 500px) {
  .merch__item-title, .merch__item-sizes, .merch__item-price {
    font-size: 14px;
  }
}
.merch__item-price {
  color: #74b461;
}
.merch__item-button {
  font-size: 18px;
  padding: 10px;
  background-color: #74b461;
}
@media screen and (max-width: 500px) {
  .merch__item-button {
    font-size: 14px;
    padding: 5px;
  }
}

.footer {
  padding: 50px 0;
  background-color: #74b461;
}
.footer__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 768px) {
  .footer__container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }
}
.footer__copyright, .footer__confidience {
  width: 270px;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: -0.54px;
}
@media screen and (max-width: 768px) {
  .footer__logo {
    order: 1;
  }
}
@media screen and (max-width: 768px) {
  .footer__confidience {
    text-align: center;
    order: 2;
  }
}
@media screen and (max-width: 768px) {
  .footer__copyright {
    text-align: center;
    order: 3;
  }
}